import glamorous from 'glamorous';
import * as React from 'react';

const LoadingImg = glamorous.img({
  animation: 'spin 0.5s infinite linear',
  height: '16px',
  width: '16px',
});

const ContainingDiv = glamorous.div(
  {
    alignItems: 'center',
    borderRadius: '4px',
    color: 'white',
    display: 'flex',
    justifyContent: 'center',
  },
  (props: { backgroundColor: string; fontSize: string; height: string; padding: string; width: string }) => ({
    backgroundColor: props.backgroundColor,
    fontSize: props.fontSize,
    height: props.height,
    lineHeight: props.height,
    padding: props.padding,
    width: props.width,
  }),
);

const VersionBox = (props: {
  bad?: boolean;
  fontSize: string;
  good?: boolean;
  height: string;
  padding: string;
  version: string;
  width: string;
}) => {
  let contents: string | React.ReactElement<any> = props.version;
  let backgroundColor = '#aaa';

  if (props.version === null) {
    contents = <LoadingImg src="/image/icon/gel-icon-loading-white.svg" />;
    backgroundColor = 'orange';
  } else if (props.version === '') {
    contents = 'N/A';
  } else if (props.bad) {
    backgroundColor = 'red';
  } else if (props.good) {
    backgroundColor = '#59bb5d';
  }

  return (
    <ContainingDiv
      backgroundColor={backgroundColor}
      fontSize={props.fontSize}
      height={props.height}
      padding={props.padding}
      width={props.width}
    >
      {contents}
    </ContainingDiv>
  );
};

export default VersionBox;



