import glamorous from 'glamorous';
import * as React from 'react';

const A = glamorous.a(
  {
    border: '1px solid rgba(0, 0, 0, 0.2)',
    borderRadius: '4px',
    color: 'black',
    flexShrink: 0,
    fontSize: '16px',
    textDecoration: 'none',
  },
  (props: { black: boolean; color: string; height: string; padding: string }) => ({
    backgroundColor: props.color,
    color: props.black ? 'black' : 'white',
    height: props.height || 'auto',
    padding: props.padding || '8px',
  }),
);

const Img = glamorous.img({
  height: '12px',
  margin: '0 8px 0 4px',
  width: '12px',
});

interface IExternalLinkProps {
  black?: boolean;
  className?: string;
  color?: string;
  height?: string;
  label: string;
  link: string;
  padding?: string;
}

const ExternalLink = (props: IExternalLinkProps) => (
  <A
    className={props.className}
    href={props.link}
    target="_blank"
    color={props.color}
    black={props.black}
    padding={props.padding}
    height={props.height}
    onClick={event => event.stopPropagation()}
  >
    <Img alt="external link" src={`/image/icon/gel-icon-external-link-${props.black ? 'black' : 'white'}.svg`} />
    <span>{props.label}</span>
  </A>
);

export default ExternalLink;



