import glamorous from 'glamorous';
import * as React from 'react';

const Button = glamorous.button(
  {
    alignItems: 'center',
    backgroundColor: 'transparent',
    border: '1px solid rgba(0, 0, 0, 0.2)',
    borderRadius: '4px',
    display: 'flex',
    flexShrink: 0,
    justifyContent: 'center',
  },
  (props: {
    backgroundColor: string;
    color: string;
    fontSize: string;
    height: string;
    padding: string;
    width: string;
    disabled: boolean;
  }) => ({
    backgroundColor: props.backgroundColor || 'transparent',
    color: props.color || 'black',
    fontSize: props.fontSize || '16px',
    height: props.height || 'auto',
    opacity: props.disabled ? 0.2 : 1,
    padding: props.padding || '8px',
    width: props.width || 'auto',
  }),
);

const Img = glamorous.img(
  {
    height: '12px',
    width: '12px',
  },
  (props: { hasLabel: boolean }) => ({
    marginRight: props.hasLabel ? '8px' : '0',
  }),
);

interface ILabelButtonProps {
  backgroundColor?: string;
  className?: string;
  color?: string;
  fontSize?: string;
  height?: string;
  image?: string;
  label: string;
  padding?: string;
  width?: string;
  disabled?: boolean;
  onClick(): any;
}

const createClickAction = (clickAction: any) => (event: any) => {
  event.stopPropagation();
  clickAction();
};

const LabelButton = (props: ILabelButtonProps) => (
  <Button
    className={props.className}
    disabled={props.disabled}
    onClick={createClickAction(props.onClick)}
    color={props.color}
    backgroundColor={props.backgroundColor}
    height={props.height}
    width={props.width}
    fontSize={props.fontSize}
    padding={props.padding}
  >
    {props.image ? <Img src={props.image} hasLabel={Boolean(props.label)} /> : null}
    <p>{props.label}</p>
  </Button>
);

export default LabelButton;



