import glamorous from 'glamorous';

const ListItemButton = glamorous.div(
  {
    alignItems: 'center',
    cursor: 'pointer',
    display: 'flex',
    flexGrow: 1,
    fontSize: '18px',
    height: '39px',
    marginBottom: '8px',
    overflow: 'hidden',
    padding: '8px',
  },
  (props: { backgroundColor: string; highlighted?: boolean }) => ({
    backgroundColor: props.backgroundColor,
    border: `1px solid rgba(0, 0, 0, ${props.highlighted ? '0' : '0.1'})`,
    boxShadow: props.highlighted ? '0px 0px 0px 3px rgba(0, 0, 0, 0.7)' : '2px 2px 4px 0 rgba(0, 0, 0, 0.05)',
  }),
);

export default ListItemButton;



