import glamorous from 'glamorous';
import * as React from 'react';

const ContainingDiv = glamorous.div({
  alignItems: 'center',
  backgroundColor: '#ff4545',
  color: 'white',
  display: 'flex',
  flexShrink: 0,
  height: '52px',
  justifyContent: 'space-between',
  padding: '8px',
});

const MessageH1 = glamorous.h1({
  fontSize: '20px',
  fontWeight: 'normal',
});

const MessageA = glamorous.a({
  color: 'white',
  textDecoration: 'underline',
});

const UpdateButton = glamorous.button({
  alignItems: 'center',
  backgroundColor: 'transparent',
  border: '1px solid rgba(255, 255, 255, 0.8)',
  borderRadius: '4px',
  color: 'white',
  display: 'flex',
  flexShrink: 0,
  fontSize: '16px',
  justifyContent: 'center',
  padding: '8px',
});

const LoadingImg = glamorous.img({
  animation: 'spin 0.5s infinite linear',
  height: '16px',
  marginRight: '8px',
  width: '16px',
});

interface IUpdateBarProps {
  outOfDate: boolean;
  updating: boolean;
  updated: boolean;
  onUpdate(): any;
}

const getMessage = (props: IUpdateBarProps) => {
  if (props.updating) {
    return 'Updating  😟';
  } else if (props.updated) {
    return 'Morph Developer Console updated sucessfully  🎉  Restart to apply updates.';
  }
  return [
    <span key="update-message-1">There is an update available for the Morph Developer Console. See </span>,
    <MessageA
      key="update-message-link"
      target="_blank"
      href="https://github.com/bbc/morph-developer-console/blob/master/docs/whats-new.md"
    >
      what's new
    </MessageA>,
    <span key="update-message-2">. 👀</span>,
  ];
};

const renderButton = (props: IUpdateBarProps) => {
  if (props.updating) {
    return <LoadingImg src="/image/icon/gel-icon-loading-white.svg" />;
  } else if (props.updated) {
    return null;
  }
  return (
    <UpdateButton className="update-button" onClick={props.onUpdate}>
      Update
    </UpdateButton>
  );
};

const UpdateBar = (props: IUpdateBarProps) =>
  props.outOfDate ? (
    <ContainingDiv className="update-bar">
      <MessageH1 className="update-header">{getMessage(props)}</MessageH1>
      {renderButton(props)}
    </ContainingDiv>
  ) : null;

export default UpdateBar;



