import glamorous from 'glamorous';
import * as React from 'react';

import LabelButton from './LabelButton';

const ContainingDiv = glamorous.div({
  alignItems: 'center',
  backgroundColor: 'rgba(0, 0, 0, 0.2)',
  bottom: 0,
  display: 'flex',
  justifyContent: 'center',
  left: 0,
  position: 'fixed',
  right: 0,
  top: 0,
});

const BoxDiv = glamorous.div({
  backgroundColor: 'white',
  borderRadius: '2px',
  boxShadow: '0 0 15px 5px rgba(0, 0, 0, 0.2)',
  display: 'flex',
  flexDirection: 'column',
  maxWidth: '750px',
  padding: '16px',
  width: '95%',
});

const HeaderDiv = glamorous.div({
  alignItems: 'center',
  display: 'flex',
  marginBottom: '16px',
});

const CloseButtonDiv = glamorous.div({});

const ContentDiv = glamorous.div({});

const TitleH1 = glamorous.h1({
  fontFamily: 'Arial',
  fontSize: '28px',
  fontWeight: 'normal',
  marginRight: 'auto',
});

interface IDialogProps {
  children?: any;
  title: string;
  onClose(): any;
}

const Dialog = (props: IDialogProps) => (
  <ContainingDiv className="dialog">
    <BoxDiv>
      <HeaderDiv>
        <TitleH1>{props.title}</TitleH1>
        <CloseButtonDiv>
          <LabelButton
            className="dialog-close-button"
            width="100%"
            image="/image/icon/no.svg"
            label=""
            onClick={props.onClose}
          />
        </CloseButtonDiv>
      </HeaderDiv>
      <ContentDiv>{props.children}</ContentDiv>
    </BoxDiv>
  </ContainingDiv>
);

export default Dialog;



