import glamorous from 'glamorous';

interface ISpacerProps {
  space?: number;
  fill?: boolean;
}

const Spacer = glamorous.div(
  {
    flexShrink: 0,
  },
  (props: ISpacerProps) => ({
    flexBasis: `${props.space || 8}px`,
    flexGrow: props.fill ? 1 : 0,
  }),
);

export default Spacer;



