import glamorous from 'glamorous';
import * as React from 'react';

const Button = glamorous.button({
  backgroundColor: 'transparent',
  border: 'none',
  flexShrink: 0,
  outline: 'none',
});

const Img = glamorous.img(
  {
    height: '16px',
    width: '16px',
  },
  (props: { loading: boolean }) => ({
    animation: props.loading ? 'spin 0.5s infinite linear' : 'initial',
  }),
);

const Span = glamorous.span({
  color: 'transparent',
  fontSize: 0,
  lineHeight: 0,
});

interface IIconButtonProps {
  className?: string;
  image?: string;
  label: string;
  loading?: boolean;
  onClick?(): null;
}

const createClickAction = (clickAction: any) => (event: any) => {
  event.stopPropagation();
  clickAction();
};

const IconButton = (props: IIconButtonProps) => (
  <Button className={props.className} onClick={createClickAction(props.onClick)}>
    <Img loading={props.loading} src={props.loading ? '/image/icon/gel-icon-loading.svg' : props.image} />
    <Span>{props.label}</Span>
  </Button>
);

export default IconButton;



